@keyframes lightning {
  0% {
    filter: brightness(1);
  }
  86% {
    filter: brightness(1);
  }
  87.75% {
    filter: brightness(1.8);
  }
  89.5% {
    filter: brightness(1.3);
  }
  93% {
    filter: brightness(3);
  }
  100% {
    filter: brightness(1);
  }
}

@keyframes toggle {
  from {
    visibility:hidden;
  }
  50% {
      visibility:hidden;
  }
  to {
      visibility:visible;
  }
}

@keyframes disappear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}